import React,{ Component } from 'react';
import Button from '../../../../components/Button/index';
import { connect } from 'react-redux';
import { actionCreators }from '../../store/index'
import {
    CommentBookWrapper,
    CommentTitle,
    CommentB

}from '../../style'
class CommentBook extends Component{
    render(){
        return(
            <CommentBookWrapper>
               <CommentTitle class="comTitle">
                    <h4>评价书本</h4>
                    </CommentTitle>
                    
                    <CommentB class="com">
                     
                        <img src="sahfa.jpg" className="bookImg" alt="商品图片"></img>
                        {/* style="display:block;border:1px solid #CCC;" */}
                        <textarea rows="10" cols="90"  placeholder="服务态度是否良好，售后服务是否到位?" name="content"></textarea>
                        
               
                        <Button className="primary middle" onClick={this.props.commentBook}>提交</Button>
                </CommentB>
                
            </CommentBookWrapper>

        )
    }
}
const maptoDipatch=(dispatch)=>({
    commentBook(data){
        dispatch(actionCreators.commentBook(data))
    }
})
export default connect(null,maptoDipatch)(CommentBook);